.login {
  border: 2px solid black;
}

.main-containerLogin {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  margin: auto;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  //   background-color: rgb(118, 118, 141);
  .aside {
    width: 50%;
    margin: auto;

    img {
      width: 80%;
    }
  }
}

.login-container {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  height: 100vh;
  padding: 10px;
  width: 30%;
  display: flex;
  background-color: #eb6b40;

  .login-div {
    padding: 20px;

    justify-content: center;
    width: 70%;
    margin: auto;

    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .img-div {
    display: flex;
    height: 20%;
    justify-content: center;

    img {
      height: 200px;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 30vh;
  width: 100%;
  h2 {
    text-align: center;
    padding: 10px;
    color: rgb(241, 241, 241);
  }

  .login-btn {
    display: flex;
    justify-content: space-between;
    button {
      padding: 10px 20px;
      border: none;
      cursor: pointer;
    }
  }
}

// HERE OUR MEDIA-QUAREIS
@media only screen and (max-width: 640px) {
  .main-containerLogin {
    .aside {
      display: none;
    }

    .login-container {
      width: 100%;
      .login-div {
        width: 100%;
      }
    }
  }

  .form {
    height: auto;
    .login-btn {
      margin-top: 1rem;
    }
  }
}
