@import url("https://fonts.googleapis.com/css2?family=Tajawal:wght@200&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

@import "./card.scss";
@import "./nav.scss";
@import "./login";

body {
  font-family: "Tajawal", sans-serif;
  overflow-x: hidden;
}

@media screen and (max-width: 480px) {
  body {
    .navigation {
      .main-navBar {
        width: 90%;
        .logo {
          padding: 4px;
          h1 {
            font-size: 1rem;
          }
        }

        ul {
          width: 50%;
          li {
            font-size: 1rem;
            a {
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #eb6b40;
  border-radius: 20px;
  border: transparent;
}
::-webkit-scrollbar-track {
  background-color: transparent;
}
