.navigation {
  width: 100%;
  background-color: rgb(255, 255, 255);
  display: flex;
  min-height: 10vh;

  .main-navBar {
    align-items: center;
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
  }

  .logo {
    padding: 10px 0 10px;

    h1 {
      font-size: 1.4rem;
      color: #eb6b40;
      font-family: "monospace";
    }
  }

  ul {
    display: flex;
    width: 20%;
    justify-content: space-around;
  }

  ul a {
    color: rgb(114, 114, 114);

    text-decoration: none;
  }

  ul li {
    font-size: 1.2rem;

    list-style: none;
  }

  @media (min-width: 480px) {
    .navigation {
      background-color: rgb(0, 174, 0);
    }
  }
}
