.main-container {
  margin: auto;
  flex-direction: row;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 80%;
  flex-direction: column;

  .formControl-div {
    padding: 10px;
    width: 100%;
    justify-content: space-between;
    display: flex;

    .main {
      margin-right: 1rem;
      position: relative;
      bottom: -2.2rem;
    }
  }

  .products {
    background-color: rgb(226, 226, 226);
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  // border: 1px solid black;
}

.card-item {
  align-items: center;
  justify-content: center;
  background-color: white;
  display: flex;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  // border: 1px solid black;
  width: 20rem;
  margin: 1rem;

  letter-spacing: 1px;
  border-radius: 20px;

  .cat-raiting {
    display: flex;
    flex-direction: column;

    h1 {
      padding: 10px;
      font-size: 1rem;
      display: block;
    }

    h2 {
      display: flex;
      flex-direction: column;

      padding: 10px;
      font-size: 0.8rem;
      color: #eb6b40;
      span {
        margin-top: 4px;
      }
    }
  }

  .img-div {
    height: 160px;
    width: 160px;
    align-items: center;
    justify-content: center;
    display: flex;

    img {
      border-radius: 50%;
      width: 80%;
    }
  }

  .cat-raiting {
    width: 60%;
  }
}

#search-Bar {
  padding: 1rem;
}

@media only screen and (max-width: 640px) {
  .main-container {
    width: 100%;
  }
}
